import classnames from 'classnames';

type PropsType = {
  picture: string,
  className?: string
}

const CardThumbnail: React.FunctionComponent<PropsType> = ({picture, className}) => {
  return (
    <div className={classnames('picture', className)}>
          <a href={picture}>
            <img src={picture} alt="" />
          </a>
          <div className="quick-view"></div>
        </div>
  )
}

export default CardThumbnail;
