import React, { useEffect } from 'react';
import { compose } from "redux";
import { connect } from "react-redux";
import { Route, Switch } from 'react-router-dom';

import { requestCategories } from './redux/actions/category';
import routes from './config/routes';
import Header from './components/Header';
import './stylesheet/style.scss';

const App: React.FunctionComponent<any> = (props) => {
  const { fetchCategories } = props;
  useEffect(() => {
    fetchCategories();
  }, []);

  return (
    <React.StrictMode>
      <Header {...props}/>
      <Switch>
        {routes.map((route) => {
          return <Route exact={route.exact} key={route.path} path={route.path} component={route.component} />
        })}
      </Switch>
    </React.StrictMode>
  );
}

const mapDispatchToProps = (dispatch: any) => ({
  fetchCategories: () => {
    dispatch(requestCategories());
  }
});

export default compose(
  connect(null, mapDispatchToProps)
)(App);


const AppProvider = React.createContext({});

const Mycontext = () => {
  const theme = React.useContext(AppProvider);
  console.log('theme', theme);
  return(
    <p>theme</p>
  )
}

export const AppContext = () => {
  const theme = 'dark'
  return (
    <AppProvider.Provider value={{theme}}>
      <Mycontext />
    </AppProvider.Provider>
  );
}
