import React from 'react';
import { compose } from "redux";
// import { connect } from "react-redux";

import withLoadingProducts from '../containers/utils/withLoading';

import Grid from './Grid';
import TitleSection from './TitleSection';
import FeaturedProduct from './FeaturedProduct';

export interface Product {
  id: string,
  cover: string,
  name: string,
  price: string,
  sales: string,
  stars: string,
  type: string
}
type PropsType = {
  products: Array<Product>,
}

const FeaturedProducts: React.FunctionComponent<PropsType>= ({ products }) => {
  return (
    <React.Fragment>
      <TitleSection title="Featured Products" subtitle="Add featured products to weekly line up" />

      <Grid containerClass="mt-20">
        {products.map((product, index) => <FeaturedProduct  product={product} key={product.id} />)}
      </Grid>
    </React.Fragment>
  );
}

export default compose(
  withLoadingProducts
)(FeaturedProducts);
