import { PROFILE } from '../constant';

let initialState = {
  status: false,
};

const profileReducer = (state = initialState, action: any) => {
  switch (action.type) {
    case PROFILE.UPDATE_STATUS:
      const status = action.payload;
      return {
        ...state,
        status
      };
    default:
      return state;
  }
};

export default profileReducer;
  