import NavItem, { Menu } from './NavItem';

type PropsType = {
  listMenu: Array<Menu>
};

const Navigation: React.FunctionComponent<PropsType>= ({ listMenu }) => {
  return (
    <nav className="header-menu menu">
      <ul className="d-flex">
        {listMenu.map((item) => <NavItem menuItem={item} key={item.id} />)}
      </ul>
    </nav>
  );
}

export default Navigation;
