type PropsType = {
  stars: number,
  counters: number,
  activeColor?: string,
  defaultColor?: string
}

const StarsRating : React.FunctionComponent<PropsType>= (props) => {
  const { stars, counters, activeColor, defaultColor } = props;
  const arr = Array.from({length: counters}, (x, i) => i+1);

  return (
    <fieldset className="rating">
      {
        arr.map((item) => {
          return (
            <label className="full" style={{color: item <= +stars ? activeColor : defaultColor}} htmlFor={"star" + item} key={"rating-label"+item}></label>
          )
        })
      }
    </fieldset>
  );
}

StarsRating.defaultProps = {
  activeColor: '#fdd835',
  defaultColor: '#d0d0d0'
}

export default StarsRating;
