import classnames from 'classnames';

type PropsType = {
  children: React.ReactNode
  cardClass?: string
  wrapperClass?: string
}

const Card: React.FunctionComponent<PropsType>= ({ children, cardClass, wrapperClass }) => {
  return (
    <article className={classnames('card', cardClass)}>
      <div className={classnames(wrapperClass)}>
        {children}
      </div>
    </article>
  );
}

export default Card;
