import { compose } from "redux";
import { connect } from "react-redux";

import { getCategoriesInfo } from '../redux/selectors/category.selector';
import { currencyFormat } from '../utils/util';
import Card from './Card';
import CardThumbnail from './CardThumbnail';
import CardContent from './CardContent';
import StarsRating from './StarsRating';

type PropsType = {
  product: any,
  categories?: any
}

interface Category {
  id: number;
  key: string;
  name: string;
}

const RenderSalesToPrice = (sales: string, price: string) => {
  let newPrice = null;

  if(sales.includes("-")) {
    if (sales.includes("%")) {
      newPrice =  +price - (+price / parseFloat(sales.replace(/[-,%]/g, "")));
    } else {
      newPrice =  +price - parseFloat(sales.replace(/[-]/g, ""));
    }
  }

  return (
    <div className="product-price-and-shipping">
      <span className={newPrice ? "regular-price" : "price"}>{currencyFormat(+price)}</span>
      {newPrice && <span className="new-price">{currencyFormat(+newPrice)}</span>}
    </div>
  )
}

const FeaturedProduct: React.FunctionComponent<PropsType> = (props) => {
  const {product, categories} = props;
  console.log('cate at product', categories);
  return (
    <Card cardClass={"col col-3"} wrapperClass={"product product-feature"}>
      <CardThumbnail picture={product.cover} />
      <CardContent className="product-content">
        <div className="cate">
          {
            categories && categories.length > 0 && (
              categories.filter((item: Category ) => item.id === +product.type).map((category: Category) => category.name)
            )
          }
        </div>
        <h3 className="name">{product.name}</h3>
        <div className="hook-reviews">
          <StarsRating
           stars={+product.stars}
           counters={5}
          />
        </div>
        { RenderSalesToPrice(product.sales, product.price) }
      </CardContent>
    </Card>
  );
}

const mapStateToProps = (state: any) => ({
  categories: getCategoriesInfo(state)
});
export default compose(
  connect(mapStateToProps, null)
)(FeaturedProduct);
