import React, { useEffect, useState } from 'react';
import { compose } from "redux";
import { connect } from "react-redux";

import { APIService } from '../../services/api';
import { ENDPOINT } from '../../services/endpoint';
import { getCategoriesInfo } from '../../redux/selectors/category.selector';

const api = new APIService();

const withLoadingProducts = (WrappedComponent: any) => {
  const mapStateToProps = (state: any) => ({
    categories: getCategoriesInfo(state)
  });

  const LoadingProductsHOC = (props: any) => {
    const [products, setProducts] = useState<any>();
    useEffect(() => {
      api.get([ENDPOINT.GET_PRODUCTS]).then(response => {
        console.log(response);
        setProducts(response);
      })
    },[]);

    if (!products) {
      return <p>Loading...</p>;
    } else if (products.length === 0) {
      return <p>Emptying</p>;
    } else {
      return <WrappedComponent products={products} {...props} />
    }
  }

  return compose(
    connect(mapStateToProps, null),
  )(LoadingProductsHOC);
}

export default withLoadingProducts;
