import { APIService } from '../../services/api';
import { ENDPOINT } from '../../services/endpoint';
import { CATEGORY } from '../constant';
const api = new APIService();

export const requestCategories = () => {  
  return (dispatch: any) => {
    return api.get([ENDPOINT.GET_CATEGORIES]).then((response) => {
      dispatch(getCategoriesSuccess(response));
    }).catch((error) => {
      dispatch(getCategoriesFailure({
        error: error,
        errorMessage: "Can't get categories"
      }))
    })
  }
}

export const getCategoriesSuccess = (payload: any) => {
  return {
    type: CATEGORY.GET_CATEGORIES_SUCCESS,
    payload
  }
}

export const getCategoriesFailure = (payload: any) => {
  return {
    type: CATEGORY.GET_CATEGORIES_FAILURE,
    payload
  }
}
  