import classnames from 'classnames';

type PropsType = {
  children: React.ReactNode
  containerClass?: string
  rowClass?: string
}

const Grid: React.FunctionComponent<PropsType> = ({children, containerClass, rowClass}) => {
  return (
    <div className={classnames('container', containerClass)}>
      <div className={classnames('row', containerClass)}>
        {children}
      </div>
    </div>
  );
}

export default Grid;
